export default function advantages() {

	const advantagesContentBlock = $('.advantages_img-block__js');
	const imgBlock = $('.advantages_img__js');
	const textBlock = $('.advantages_img-text__js');

	if ($(window).width() > '767') {

		// функция которая срабатывает при изменении активного элемента. добавляет анимацию изчезновления\появления 
		// и также туда атрибутами прокидываются данные для отображения в блоке
		const changeActiveItem = (src, text) => {
			advantagesContentBlock.animate({ opacity: 0 }, 200);
			setTimeout(() => {
				imgBlock.attr('src', src);
				textBlock.text(text);
				advantagesContentBlock.animate({ opacity: 1 }, 200);
			}, 200);
		}

		if ($('.advantages__js').length > 0) {

			for (let i = 0; i < $('.advantages_list-item__js').length; i++) {
				document.querySelectorAll('.advantages_list-item__js')[i].addEventListener('click', function () {

					// если этот элемент уже активен - ничего не делать
					if ($(this).hasClass('active')) return

					else {

						$('.advantages_list-item__js').removeClass('active');
						$(this).addClass('active');

						if (i === 0) {
							changeActiveItem(
								'./img/advantages-img1.svg',
								'Все работы выполняются на тестовом поддомене и переносятся на основной сайт только после тщательного тестирования.'
							)
						} else if (i === 1) {
							changeActiveItem(
								'./img/advantages-img2.svg',
								'Оплата за продвижение, только в случае нахождения сайта в ТОПе поисковой выдачи. Условия работы согласовываются с заказчиком индивидуально'
							)
						} else if (i === 2) {
							changeActiveItem(
								'./img/advantages-img3.svg',
								'В нашей команде более 50 узкоспециализированных специалистов — это позволяет каждому заниматься тем, в чем он профи!'
							)
						} else if (i === 3) {
							changeActiveItem(
								'./img/advantages-img4.svg',
								'Персональный менеджер — человек, проникающий в суть вашего бизнеса. Он координирует внутреннюю работу, делая её четкой и отлаженной.'
							)
						} else if (i === 4) {
							changeActiveItem(
								'./img/advantages-img5.svg',
								'Оплата за результат, все изменения на сайте согласовываются, ежемесячный отчет по динамике сайта, работа по договору'
							)
						} else if (i === 5) {
							changeActiveItem(
								'./img/advantages-img6.svg',
								'Для нас каждый сайт индивидуален, мы не используем шаблонные решения!'
							)
						} else if (i === 6) {
							changeActiveItem(
								'./img/advantages-img7.svg',
								'Мы не собираем ядро по 1500-3000 ключевых слов, мы берем только то, что может увеличить ВАШИ ПРОДАЖИ.'
							)
						}
					};
				});
			};
		};
	}

	// мобильная версия
	else {

		$('.advantages_list__js').slick({
			infinite: false,
			slidesToShow: 1,
			slidesToScroll: 1,
			arrows: false,
			dots: true,
			appendDots: $('.advantages__js'),
		})
	}
};