export default function tariffs() {

	$(document).on('click', '.tariffs_type-item__js', function (e) {
		$(".tariffs_type-item__js.active").removeClass('active');
		$(this).addClass('active');
	})

	const tariffInfoBlock = $('.tariffs_info-anim__js');
	const tariffTitle = $('.tariffs_info-title__js');
	const secondPriceBox = $('.tariffs_info-price-item__js');
	const tariffPriceFirst = $('.tariffs_info-price-box-first__js');
	const tariffRequests = $('.tariffs_info-price-box-second__js');
	const tariffText = $('.tariffs_info-text__js');

	// если нужно будет менять модалки в зависимости от выбранного тарифа
	const tariffButton = $('.tariffs_info-button__js');
	const tariffTransparentButton = $('.tariffs_info-button__transparent__js');

	// функция которая срабатывает при изменении активного элемента. добавляет анимацию изчезновления\появления 
	// и также туда атрибутами прокидываются данные для отображения в блоке
	const changeActiveTariffs = (name, price, isSecondPriceBox, requests, text) => {
		tariffInfoBlock.animate({ opacity: 0 }, 200);
		setTimeout(() => {
			tariffTitle.text(name);
			tariffPriceFirst.html(price);
			if (!isSecondPriceBox) secondPriceBox.css('display', 'none')
			else secondPriceBox.css('display', 'flex');
			tariffRequests.text(requests);
			tariffText.text(text)
			tariffInfoBlock.animate({ opacity: 1 }, 200);
		}, 200);
	}

	if ($('.tariffs_type-item__js').length > 0) {

		for (let i = 0; i < $('.tariffs_type-item__js').length; i++) {
			document.querySelectorAll('.tariffs_type-item__js')[i].addEventListener('click', function () {

				// если этот элемент уже активен - ничего не делать
				if ($(this).hasClass('active')) return

				else {

					$('.tariffs_type-item__js.active').removeClass('active');
					$(this).addClass('active');

					if (i === 0) {
						changeActiveTariffs(
							'light',
							'от 15 000 <span class="rouble">o</span>',
							true,
							'до 100',
							'Хорошо подойдет региональным сайтам с умеренной конкуренцией. Если это про вас, то скорее всего в течение 2-4 месяцев вы займете топ-10 выдачи в своем регионе по 80-90% поисковых запросов.',
						)
					} else if (i === 1) {
						changeActiveTariffs(
							'middle',
							'от 25 000 <span class="rouble">o</span>',
							true,
							'до 500',
							'Хорошо подойдет региональным сайтам с высокой конкуренцией; сайтам со среднеконкурентными тематиками с регионом продвижения Москва, Екатеринбург, Питер и др., а также сайтам, работающим по всей РФ в сфере с умеренной и высокой конкуренции'
						)
					} else if (i === 2) {
						changeActiveTariffs(
							'Exclusive',
							'По согласованию',
							false,
							'',
							'В данном тарифе учитывается не количество ключевых запросов, а составляется план роста посещаемости сайта. Оптимальный вариант для крупных сайтов с большим количеством ключевых запросов, а также продвигающихся по всей России, эффективен в том числе для интернет-магазинов.'
						)
					};
				};
			});
		};
	};

	// изменение фона в зависимости от ширины в мобильной версии

	const tariffRectangle = $('.tariffs_rectangle__js');

	if ($(window).width() <= '767' && $(window).width() > '320') {

		function setTariffRectanglePosition(e) {
			let actualWidth = $(window).width()

			let topPosition = -(actualWidth - 320) * 0.8;
			let actualHeight = 295 + (actualWidth - 320) * 0.95;
			let cssValues = {
				"top": topPosition,
				"height": actualHeight
			}
			tariffRectangle.css(cssValues);
		}

		setTariffRectanglePosition();
		window.addEventListener("resize", setTariffRectanglePosition, false);
	}
};