export function modal() {
	let modalBtn = document.querySelectorAll('[data-modalbtn]');

	modalBtn.forEach(function (item) {
		item.addEventListener('click', function (e) {

			e.preventDefault;

			let modalName = item.dataset.modalbtn,
				modalWindow = document.querySelector(`[data-modal="${modalName}"]`);

			modalWindow.classList.add('is-active');
			setTimeout(function () {
				modalWindow.classList.add('is-visible');

				document.body.style.overflowY = 'hidden';
				document.documentElement.style.overflowY = 'hidden';
			}, 300);

			modalWindow.addEventListener('click', function (event) {

				if (event.target.classList.contains('js-modalClose')) {
					modalWindow.classList.remove('is-visible');
					setTimeout(function () {
						modalWindow.classList.remove('is-active');

						document.body.style.overflowY = '';
						document.documentElement.style.overflowY = '';
					}, 300);
				}
			});
		});
	});
}